@import './shared.scss';

body {
  background-color: $bg-color;
  font-family: $primary-font-stack;
  font-style: normal;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}
